/*eslint-disable*/
import { useNavigate } from 'react-router-dom';
import { useDispatch } from "react-redux"
import { setWatched } from './store/watchedSlice';
import { setIsHome } from './store/ishomeSlice';

//  
const Product = (props) => {
    //   
    let navigate = useNavigate()

    let dispatch = useDispatch()

    let formatPrice = " "+ addComma(props.data.price+"")
    let seq = props.data.id; //  Detail ?? => URL 

    return (
        <>
            <div className="col-md-4" id="prd-container">
                <img src={process.env.PUBLIC_URL + "/shoes/product"+(props.seq+1)+".png"} alt="product" id="prd-img" 
                    onClick={()=>{
                        addWatched(seq) // localStorage 
                        dispatch(setWatched()) // state   localStorage 
                        //   
                        navigate('/detail/'+seq)
                        dispatch(setIsHome(false))
                }}/>
                <h5 id="prd-title"
                    onClick={()=>{
                        addWatched(seq) // localStorage 
                        dispatch(setWatched()) // state   localStorage 
                        //   
                        navigate('/detail/'+seq)
                        dispatch(setIsHome(false))
                }}>{props.data.title}</h5>
                <p id="prd-price">{formatPrice}</p>
            </div>
        </>
    )
}


//    localStorage  
const addWatched = (itemNum) => {
    let watched = JSON.parse(localStorage.getItem('watched'))
    if (watched == null) {return;}
    watched.unshift(itemNum)
    let uniqeWatched = new Set(watched) // 
    localStorage.setItem('watched',JSON.stringify([...uniqeWatched]))
}


//   (,)   
const addComma = (value, defaultStr = '') => {
    // null ,    parameter   (default  : '')
    if(value == null || value.length == 0 || value == undefined) {
        return defaultStr
    }

    let integerAndDecimal = value.split('.') //  .   
    let integer = integerAndDecimal[0]; // 
    let decimal = integerAndDecimal.length > 1 ? '.' + integerAndDecimal[1] : ''; //    .xxx ,  
    
    let rgx = /(\d+)(\d{3})/; //       
    while (rgx.test(integer)) { // test() :      Boolean 
        integer = integer.replace(rgx, '$1' + ',' + '$2') //   
    }
    // let result = integer+decimal; //    
    let result = integer //  
    return result
}


export default Product;