/*eslint-disable*/
import { createSlice } from '@reduxjs/toolkit'

// createSlice  useState 
let cart = createSlice({
    name : '',
    initialState : [
         {
            id : 0,
            name : "Nike Air Force 1 '07 Low White",
            count : 2
        },
        {
            id : 1,
            name : "New Balance 993 Made in USA Grey",
            count : 1
        }   
    ],
    reducers : {
        addCount(state, action) {
            let num = state.findIndex((a)=>{ return a.id === action.payload }) // index 
            state[num].count += 1
        },
        subCount(state, action) {
            let num = state.findIndex((a)=>{ return a.id === action.payload }) // index 
            state[num].count -= 1
        },
        addItem(state, action) {
            let isNew = true
            let item = action.payload
            state.map((a,i)=>{
                //    
                if(a.id === item.id){
                    console.log("add Cart ")
                    a.count += item.count
                    isNew = false
                }
            })
            //   
            if (isNew) {
                console.log("add Cart ")
                state.push(item)
            }
        },
        subItem(state, action) {
            let item = action.payload
            //  id    state  ( == )
            return state.filter(cart => cart.id !== item.id)
        }
    }
})

export let { addCount, subCount, addItem, subItem } = cart.actions
export default cart